-->
<template>
  <backHomeBtn>
    <template>
      <span v-if="resultPosition[0].lat">
        坐标： {{ resultPosition[0].lng }} ，{{ resultPosition[0].lat }}
      </span>
      <el-button @click="openMap" size="small"> 开始定位</el-button>
      <getPointDialog ref="getPointDialog" @backData="getMapPoint" />
    </template>
  </backHomeBtn>
</template>

<script>
import getPointDialog from "@/views/tdMapMakePoint/getPointDialog";
export default {
  name: "tdMapMakePoint",
  components: { getPointDialog },
  props: {},
  data() {
    return {
      resultPosition: [
        {
          lat: null, // 纬度
          lng: null, // 经度
        },
      ],
    };
  },
  methods: {
    /**
     * @Event 方法
     * @description: 打开打点弹窗
     * */
    openMap() {
      this.$refs.getPointDialog.showDialog(this.resultPosition);
    },

    /**
     * @Event 方法
     * @description: 接收弹窗返回的值
     * */
    getMapPoint(data) {
      if (data.length > 0) {
        this.$set(this.resultPosition[0], "lat", data[0].lat.toString());
        this.$set(this.resultPosition[0], "lng", data[0].lng.toString());
      } else {
        this.resultPosition = [];
      }
      console.log(this.resultPosition, 157);
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped></style>
